import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: { name: 'AdAccounts' },
  },
  {
    path: '/adaccounts',
    name: 'AdAccounts',
    component: () => import('../views/AdAccounts.vue')
  },
  {
    path: '/designs',
    name: 'Designs',
    component: () => import('../views/Designs.vue')
  },
  // {
  //   path: '/casper',
  //   name: 'Casper1',
  //   component: () => import('../views/Casper1.vue')
  // },
  // {
  //   path: '/danny1',
  //   name: 'Danny1',
  //   component: () => import('../views/Danny1.vue')
  // },
  // {
  //   path: '/danny2',
  //   name: 'Danny2',
  //   component: () => import('../views/Danny2.vue')
  // },
  {
    path: '/AdAccountsIntroPopup',
    name: 'AdAccountsIntroPopup',
    component: () => import('../views/AdAccountsIntroPopup.vue')
  },
  {
    path: '/AdAccountsKpiOverview',
    name: 'AdAccountsKpiOverview',
    component: () => import('../views/AdAccountsKpiOverview.vue')
  },
]

const router = new VueRouter({
  routes
})

export default router
